import React from 'react';
import styled from 'styled-components';
import NavbarItem from './NavbarItem';

const navLinks = [
  { linkName: '', linkHref: '#' },
  { linkName: '', linkHref: '#' },
  { linkName: '', linkHref: '#' },
  { linkName: '', linkHref: '#' },
  { linkName: '', linkHref: '#' },
];

const Ul = styled.ul`
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  @media screen and (min-width: 780px) {
    flex-direction: row;
    align-items: center;
    height: 100%;
    width: 300px;
    justify-content: space-between;
  }
`;

const NavbarUl = () => {
  return (
    <Ul>
      {navLinks.map((item) => (
        <NavbarItem
          linkHref={item.linkHref}
          linkName={item.linkName}
          key={item.linkName}
        />
      ))}
    </Ul>
  );
};

export default NavbarUl;
